/* OVERRIDE DAY OF WEEK STYLES TO ONLY SHOW FIRST CHARACTER */
sp-date-range-pay-period {
  .ngb-dp-weekday {
    text-transform: uppercase;
    font-size: 11px;
    color: $white !important;
    font-style: normal !important;
    padding-left: 7px;
    @include user-select(none);
  }

  .ngb-dp-month-name {
    background-color: $white !important;
  }

  .ngb-dp-week .ngb-dp-weekday:nth-child(6):first-letter,
  .ngb-dp-week .ngb-dp-weekday:nth-child(7):first-letter {
    color: #c6c6c6 !important;
  }

  .ngb-dp-weekday:first-letter {
    color: $spdarkgray;
  }

  .ngb-dp-day:focus {
    outline: 0 !important;
  }

  .ngb-dp-navigation-chevron {
    border-color: $spmediumgray !important;
    border-width: 0.1em 0.1em 0 0 !important;
  }

  .ngb-dp-header,
  .ngb-dp-week {
    background-color: $white !important;
  }

  .pay-period-label {
    font-size: 13px;
    line-height: 13px;
    font-weight: 500;
    color: #879a8c;
    margin: 3px 0 3px 0;
    text-transform: uppercase;
  }

  .pay-period-date {
    font-size: 19px;
    color: $spdarkgray;
  }

  .custom-day {
    text-align: center;
    padding: 0.185rem 0.25rem;
    display: inline-block;
    height: 2rem;
    width: 2rem;

    &.focused {
      background-color: $white;
    }

    &.range,
    &.period-date:hover {
      background-color: #dbe5c3;
      color: $white;
    }

    &.range:not(.faded) {
      background-color: #b1c389 !important;
    }

    &.faded {
      background-color: #dbe5c3;
    }
  }

  div:not(.disabled) > .check-day-hover:hover,
  .check-day {
    opacity: 1 !important;
    background-color: #ff7a00 !important;
    border: 1px solid #ff7a00 !important;
    color: #fff;
  }

  .cursor-disabled {
    cursor: not-allowed !important;
  }

  .period-date:hover {
    background-color: #b1c389 !important;
  }

  ngb-datepicker {
    .ngb-dp-day.disabled {
      color: #c6c6c6;
    }
  }

  /* Status/Nav Section */
  .pay-period-labels {
    width: 100%;
  }

  .pay-period-section {
    padding: 15px;
    background-color: #f3f3f3;
    cursor: pointer;
    height: 75px;
  }

  .ngb-dp-arrow-btn {
    height: auto;
  }

  /* Border between pay period blocks */
  .pay-period-section:nth-child(2) {
    border-top: 1px solid #dee0e3;
    border-bottom: 1px solid #dee0e3;
  }

  .pay-period-labels > div > h6:first-child {
    margin: 0;
  }

  .no-gutters {
    margin-right: 0;
    margin-left: 0;
    > .col,
    > [class*='col-'] {
      padding-right: 0;
      padding-left: 0;
    }
  }

  /* Semi-Monthly Form Controls */
  .frequency-semi-monthly {
    .date-field {
      width: 9em !important;
    }
    .input-group-append {
      margin-left: -3px;
    }
  }

  /* Responsive styles */

  @media screen and (max-width: 730px) {
    // Repositioning tooltips to prevent them extending offscreen on mobile devices
    #tooltipZone {
      position: absolute;
      left: 0;
      margin-left: -125px;
      top: 30px;
    }

    .frequency-semi-monthly #tooltipZone {
      margin-left: -300px;
    }
  }

  @media screen and (max-width: 700px) {
    .pay-period-container > .row {
      margin-right: 0;
      margin-left: 0;
      > .col,
      > [class*='col-'] {
        padding-right: 0;
        padding-left: 0;
      }
    }
  }

  @media screen and (max-width: 615px) {
    .pay-period-labels {
      padding-left: 0;
    }

    .frequency-semi-monthly .date-field {
      max-width: 8em !important;
    }
  }

  @media screen and (max-width: 576px) {
    .frequency-semi-monthly .date-field {
      width: 9em !important;
    }

    .pay-period-section {
      border: 1px solid #dee0e3;

      &:nth-child(2) {
        border-left: none;
        border-right: none;
      }
    }

    .pay-period-labels {
      padding-right: 0;
      margin-bottom: 20px;
    }

    .pay-period-label {
      font-size: 12px;
      font-weight: 700;
    }
  }

  @media screen and (max-width: 490px) {
    .pay-period-labels > row {
      margin-right: -1em;
    }

    .pay-period-label {
      font-size: 10px;
    }

    .pay-period-container {
      padding-left: 0;
      padding-right: 0;
    }

    .ngb-dp-month:first-child .ngb-dp-week {
      padding-left: 0 !important;
    }

    .pay-period-date {
      font-size: 17px;
    }

    sp-radio-group {
      width: 100%;
    }
  }

  @media screen and (max-width: 457px) {
    .ngb-dp-months {
      margin-left: -10px;
    }

    .pay-period-labels > row {
      margin-right: -40px;
    }

    .frequency-semi-monthly .ngb-dp-months {
      margin-left: 0px !important;
    }
  }
}
