// Standard Card
.sp-card {
  margin-bottom: 1rem;
  padding: 2rem;
  border-radius: 0.125rem;
  background-color: $spoffwhite;
  border: 1px solid $splightgray2;
  display: flex;

  @media screen and (max-width: 992px) {
    width: 100%;
  }

  .sp-card-content {
    width: 100%;

    .sp-card-header {
      font-weight: bold;
    }

    .sp-card-subheader {
      font-size: 0.9rem;
      margin-bottom: 4px;
    }
  }

  &.sp-card-helper {
    span {
      font-size: 0.9rem;
      font-weight: 600;
    }
  }
}

.card-center {
  width: 30rem;
  margin: auto;
}

// Boostrap Card Class Customization
.card {
  margin-bottom: 1rem;
}

// Checkbox Cards
$checkBoxBrightnessChange: 3%;
$checkBoxAnimationTime: 0.7s;

.checkbox-card-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 20px;
}

.checkbox-card {
  width: 200px;
  height: 150px;
  position: relative;
  width: 210px;
  margin-bottom: 20px;

  label {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    font-size: 13px;
    display: flex;
    font-weight: normal;
    cursor: pointer;
  }

  *:not(.add-option) {
    font-family: 'Open Sans', sans-serif !important;
    color: $spdarkgray !important;
  }

  input[type='checkbox'] {
    visibility: hidden;

    /* Checked Styles */
    &:checked + label {
      .add-option {
        display: none;
      }

      .icon-animate-fill {
        fill: $sureSelectedBlue !important;
      }
      .selection-column {
        background-position: 0 -100%;
      }
      .selection-box {
        background-color: #f1f6f9;
        color: $sureSelectedBlue;
      }

      .checkbox-card-check {
        display: block;
      }
    }
  }

  .checkbox-card *:not(.add-option) {
    font-family: 'Open Sans', sans-serif !important;
    color: $spdarkgray !important;

    /* Hover Styles */
    &:hover {
      .icon-animate-fill {
        fill: lighten($sureLightGray, $checkBoxBrightnessChange) !important;
        transition: $checkBoxAnimationTime ease all;
      }
      *:not(.add-option) {
        color: lighten($spdarkgray, 20%) !important;
      }
      .checkbox-card-content {
        border: 1px solid lighten($sureLightGray, $checkBoxBrightnessChange);
        border-left: none;
      }
      .selection-column {
        background-image: linear-gradient(
          to bottom,
          lighten($sureLightGray, $checkBoxBrightnessChange) 50%,
          lighten($sureSelectedBlue, $checkBoxBrightnessChange) 50%
        );
      }
    }
  }
}

.checkbox-card-check {
  display: none;
}

.checkbox-card input[type='checkbox'] {
  visibility: hidden;
}

.selection-column {
  height: 100%;
  width: 30px;
  background-size: 100% 201%;
  background-image: linear-gradient(to bottom, $splightgray2 50%, $spbrightblue 50%);
  transition: background-position $checkBoxAnimationTime;
}

.selection-box {
  width: 28px;
  height: 28px;
  margin: 1px auto 0 auto;
  background: $white;
  display: flex;
  align-items: center;
  justify-content: center;

  > .add-option {
    font-size: 23px;
    color: $sureLightGray;
  }
}

.checkbox-card-content {
  height: 100%;
  padding: 10px 15px 15px 15px;
  border: 1px solid $splightgray2;
  border-left: none;

  .description {
    font-size: 12px !important;
    line-height: 16px;
  }
}

.checkbox-card-header {
  display: flex;
  font-size: 14px;
  justify-content: flex-start;
  align-items: center;
  min-height: 55px;
  margin-bottom: 5px;

  .title {
    font-size: 13px !important;
    line-height: 16px;
    margin-left: 15px;
  }

  > .icon-container {
    width: 32px;
    height: 32px;
    flex: 0 0 auto;
  }
}

/* For the icon color effect when option is checked */
.icon-animate-fill {
  fill: $splightgray2 !important;
  transition: $checkBoxAnimationTime ease all;
}

/* Hover styles */
.checkbox-card:hover {
  .icon-animate-fill {
    fill: lighten($splightgray2, $checkBoxBrightnessChange) !important;
    transition: $checkBoxAnimationTime ease all;
  }
  *:not(.add-option) {
    color: lighten($spdarkgray, 20%) !important;
  }
  .checkbox-card-content {
    border: 1px solid lighten($splightgray2, $checkBoxBrightnessChange);
    border-left: none;
  }
  .selection-column {
    background-image: linear-gradient(
      to bottom,
      lighten($splightgray2, $checkBoxBrightnessChange) 50%,
      lighten($spbrightblue, $checkBoxBrightnessChange) 50%
    );
  }
}

/* Checked Styles */
.checkbox-card input[type='checkbox']:checked + label {
  .add-option {
    display: none;
  }

  .icon-animate-fill {
    fill: $spbrightblue !important;
  }
  .selection-column {
    background-position: 0 -100%;
  }
  .selection-box {
    background-color: $spoffwhite2;
    color: $spbrightblue;
  }

  .checkbox-card-check {
    display: block;
  }
}

@media screen and (max-width: 780px) {
  .checkbox-card-group {
    justify-content: center;
  }
  .checkbox-card {
    margin: 0 20px 20px 20px;
  }
}

@media screen and (max-width: 580px) {
  .checkbox-card-group {
    justify-content: center;
    flex-direction: column;
  }
  .checkbox-card {
    margin: 0 auto 20px auto;
  }
}
